import React, { Component } from 'react';
import { connect } from 'react-redux';
import { ReactComponent as DonateIcon } from './media/heart.svg';
import Swal from 'sweetalert2';
import add_pending_sentence from '../../redux/actions/voice_interface/add_pending_sentence';
import './style.css';
const finally_audio_url = require('./media/finally.wav');
const finally_audio = new Audio(finally_audio_url);


class Donate extends Component {
    state = {
        show: false
    }

    clickHandler(event) {
        event.stopPropagation();
        Swal.fire({
            title: "Support the project",
            html:'<div id="ppbtn"></div>',
            confirmButtonText: "I can't right now"
        });
        document.getElementsByClassName("swal2-container")[0].addEventListener('click', function (ev) {
            ev.stopPropagation();
        });
        window.paypal.Buttons().render('#ppbtn');
        finally_audio.play();
    }

    componentDidMount() {
        const me = this;
        document.getElementById('donate-btn').addEventListener('click', function (ev) {
            me.clickHandler(ev);
        });
    }

    componentDidUpdate() {
        
        const me = this;
        document.getElementById('donate-btn').addEventListener('click', function (ev) {
            me.clickHandler(ev);
        });
        
    }

    render() {
        return (
            <div>
                <button id="donate-btn" className="btn btn-secondary" >
                    Support my work .
                    <DonateIcon />.
                </button>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
    }
};

const mapDispatchToProps = (dispatch) => (
{
    add_voice_sentence: (sentence) => {
        dispatch(add_pending_sentence(sentence));
    }
});


export default connect(mapStateToProps, mapDispatchToProps)(Donate);
